.popup__form {
    width: 430px;
    background-color: #ffffff;
    box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
    border-radius: 10px;
    box-sizing: border-box;
}

@media screen and (max-width: 768px) {
    .popup__form {
        width: 282px;
        left: 0;
        bottom: 0;
    } 
 }